import {
  T_ConversionRates,
  T_FavoriteCodes,
  T_FavoritesWithValue
} from "components/UnitConverter/types";
import { round, toNumber } from "helpers/number";
import { T_StringOrNumber } from "types/common";
import { T_ConversionFormulas } from "types/tools/unitConverter";

export function sanitizeNumericInput(value: string) {
  return (
    value
      // allow only numbers (with/without decimal values)
      .replace(/[^0-9.]/g, "")
      // allow only one decimal
      .replace(/(\..*?)\..*/g, "$1")
      // allow leading zero only for decimal values
      .replace(/^0(\d)/, "$1")
  );
}

export function getDefaultFavorites(
  favoriteCodes: T_FavoriteCodes
): T_FavoritesWithValue {
  return favoriteCodes.map((code) => ({ code, value: "" }));
}

export function calcValueForFavorites({
  favorites,
  conversionRates,
  conversionFormulas,
  inputCode,
  inputValue
}: T_CalcValueForFavoritesFnParams): T_FavoritesWithValue {
  return favorites.map((favItem) => ({
    ...favItem,
    value: calcValue({
      conversionRates,
      conversionFormulas,
      inputCode,
      targetCode: favItem.code,
      inputValue
    })
  }));
}

export function calcValue({
  conversionRates,
  conversionFormulas,
  inputCode,
  targetCode,
  inputValue
}: T_CalcValueFnParams): T_StringOrNumber {
  if (conversionFormulas) {
    return calcValueBasedOnFormula({
      conversionFormulas,
      inputCode,
      targetCode,
      inputValue
    });
  } else if (conversionRates) {
    return calcValueBasedOnRatio({
      conversionRates,
      inputCode,
      targetCode,
      inputValue
    });
  } else {
    return inputValue;
  }
}

/**
 * Assume:
 *    base code (of master data): BBB
 *    input code: CCC
 *    input value: x
 *    target code: YYY
 *
 * and we know (from master data):
 *    1 BBB = n1 CCC
 *    1 BBB = n2 YYY
 *
 * then,
 *               1 CCC  =  (1 / n1) BBB
 *               x CCC  =  x * (1 / n1) BBB
 *    x * (1 / n1) BBB  =  n2 * x * (1 / n1) YYY
 *               x CCC  =  n2 * x * / n1 YYY
 */
function calcValueBasedOnRatio({
  conversionRates,
  inputCode,
  targetCode,
  inputValue
}: { conversionRates: T_ConversionRates } & T_CalcValueFnParams) {
  const x = toNumber(inputValue);
  const n1 = conversionRates[inputCode];
  const n2 = conversionRates[targetCode];

  return round((n2 * x) / n1);
}

function calcValueBasedOnFormula({
  conversionFormulas,
  inputCode,
  targetCode,
  inputValue
}: { conversionFormulas: T_ConversionFormulas } & T_CalcValueFnParams) {
  let targetValue: T_StringOrNumber = 0;

  if (inputCode === conversionFormulas.baseUnitCode) {
    return conversionFormulas.fromBaseTo[targetCode](inputValue);
  }

  targetValue = conversionFormulas.toBaseFrom[inputCode](inputValue);

  if (targetCode === conversionFormulas.baseUnitCode) {
    return targetValue;
  } else {
    return conversionFormulas.fromBaseTo[targetCode](targetValue);
  }
}

type T_CalcValueForFavoritesFnParams = Omit<
  T_CalcValueFnParams,
  "targetCode"
> & {
  favorites: T_FavoritesWithValue;
};

type T_CalcValueFnParams = {
  inputCode: string;
  targetCode: string;
  inputValue: T_StringOrNumber;
  conversionRates?: T_ConversionRates;
  conversionFormulas?: T_ConversionFormulas;
};
